<template>
  <div>
    <template>
    <el-button type="primary" @click="get_movie" ico="el-icon-search" size="small">搜索</el-button>
     <!-- <el-button type="primary" v-on:click="clear_content('',movie_list)" icon="el-icon-delete" size="small">清除</el-button> -->
        </template>
      <el-table :data="movie_list"> 
        <el-table-column prop="id" label="ID" />
        <el-table-column prop="title" label="电影名" />
        <el-table-column prop="alt" label="URL" />
        <!-- <el-table-column  label="头像" >
        <img prop=avatars.large alt="图片"/>
        </el-table-column> -->
        <el-table-column  label="操作" >
          <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="small">编辑</el-button>
         <el-button type="primary" v-on:click="clear_content(scope.$index,movie_list)" icon="el-icon-delete" size="small">清除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="block">
      <el-pagination
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :total="totalCount">
    </el-pagination>
        </div> 
      </div>
      
</template>



<script>
import axios from 'axios'
export default {
  data(){
    return{
      movie_list:[],
      currentPage:1,
      pagesize:10,
      totalCount:0
    }
            
  },
  created: function(){
      //  this.get_movie()
  },
  methods: {
    get_movie: function(){
        axios.get('http://localhost:8080/src/assets/'+this.currentPage+'.json').then(res => {
            this.movie_list = res.data.subjects;
            this.totalCount=res.data.total;

        })
    },
    //  handleSizeChange:function(val) {
    //     this.pagesize=val;
    //   },
      handleCurrentChange: function(val) {
        this.currentPage=val;
        this.get_movie()
      }
  }
}
</script>



